import * as types from "../constants/cart.constants";
import {
  LOGIN_SUCCESS,
  GOOGLE_LOGIN_SUCCESS,
  LOGOUT,
} from "../constants/user.constants";

// 카트 초기 상태 정의
const initialState = {  
  loading: false,           //동작이 진행 중인지 나타냅니다.
  error: "",                // 발생한 오류 메시지를 저장합니다
  cartList: [],             // 카트에 있는 항목 목록을 저장합니다
  selectedItem: {},         // 현재 선택된 항목을 저장합니다 (있는 경우)
  cartItemCount: 0,         // 카트에 있는 항목의 총 수를 저장합니다
  totalPrice: 0,            // 카트에 있는 항목의 총 가격을 저장합니다
};


// 카트 리듀서 함수 정의
function cartReducer(state = initialState, action) {
  const { type, payload } = action; // 액션에서 유형과 페이로드를 추출합니다

  switch (type) {
    case LOGOUT: {
      // 사용자가 로그아웃할 때, cartItemCount를 0으로 재설정합니다
      return { ...state, cartItemCount: 0 };
    }
    case types.ADD_TO_CART_REQUEST:
    case types.GET_CART_LIST_REQUEST:
    case types.DELETE_CART_ITEM_REQUEST:
    case types.UPDATE_CART_ITEM_REQUEST:
    case types.GET_CART_QTY_REQUEST:
      // 특정 요청이 진행 중일 때, loading을 true로 설정합니다
      return { ...state, loading: true };

    case types.ADD_TO_CART_SUCCESS:
    case types.DELETE_CART_ITEM_SUCCESS:
      // 항목이 성공적으로 카트에 추가되거나 삭제될 때,
      // cartItemCount를 업데이트하여 카트의 항목 수를 갱신합니다
      return { ...state, loading: false, cartItemCount: payload };
    case types.UPDATE_CART_ITEM_SUCCESS:
      // 카트에 있는 항목이 성공적으로 업데이트될 때,
      // 업데이트된 데이터를 기반으로 cartList와 totalPrice를 업데이트합니다
      return {
        ...state,
        loading: false,
        cartList: payload,
        totalPrice: payload.reduce(
          (total, item) => (total += item.productId.price * item.qty),
          0
        ),
      };
    case types.GET_CART_LIST_SUCCESS:
      // 카트 목록이 성공적으로 가져올 때,
      // 가져온 데이터를 기반으로 cartList와 totalPrice를 업데이트합니다
      return {
        ...state,
        loading: false,
        cartList: payload,
        totalPrice: payload.reduce(
          (total, item) => (total += item.productId.price * item.qty),
          0
        ),
      };
    case types.GET_CART_QTY_SUCCESS:
      // 카트 항목 수가 성공적으로 가져올 때,
      // 가져온 항목 수로 cartItemCount를 업데이트합니다
      return { ...state, cartItemCount: payload };
    case types.ADD_TO_CART_FAIL:
    case types.GET_CART_LIST_FAIL:
    case types.DELETE_CART_ITEM_FAIL:
    case types.UPDATE_CART_ITEM_FAIL:
    case types.GET_CART_QTY_FAIL:
      // 요청에서 실패할 때, error 필드에 오류 메시지를 업데이트합니다
      return { ...state, loading: false, error: payload };
    default:
      // 위의 액션 중 어느 것에도 해당하지 않으면 현재 상태를 그대로 반환합니다
      return state;
  }
}
export default cartReducer; // cartReducer 함수를 내보냅니다
