import * as types from "../constants/apartment.constants";

const initialState = {
  loading: false,
  userType: "", //아파트등록_유저타입설정
  error: "",
  selectedInternalSize: "", //아파트등록_평수선택
  //아파트등록_주소작성
  address: "",
  detailAddress: "", //상세주소
  transactionMethod: "", //거래방법
  photos: "", //사진등록
  direction: "", //아파트 방향
  propertyInformation: "", //매물정보
  maintenanceFee: "", //관리비 입력
  propertyDetail: "", //매물세부 정보 가능여부
  moveIn: "", //입주날짜 선택
  interiorOption: "", //내부옵션 선택
  propertyFeatures: "", //매물장점
  introduction: "", //매물 소개
  //error: null, //오류 메시지를 저장할 속성 추가 <-나중에 지워야 함
  apartments: [],
};

function apartmentReducer(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    //유저 타입설정
    case types.SET_USER_TYPE:
      return { ...state, userType: payload };
    case types.SET_USER_TYPE_SUCCESS:
      return { ...state, userType: payload };
    case types.SET_USER_TYPE_FAIL:
      return { ...state, error: payload };
    //평수선택
    case types.SET_INTERNAL_SIZE:
      return { ...state, selectedSize: payload };
    /* case types.SET_INTERNAL_SIZE_SUCCESS:
      return { ...state, selectedSize: payload };
    case types.SET_INTERNAL_SIZE_FAIL:
      return { ...state, selectedSize: payload }; */

    //주소작성
    case types.SET_ADDRESS:
      return { ...state, address: payload };
    case types.SET_ADDRESS_SUCCESS:
      return { ...state, address: payload };
    case types.SET_ADDRESS:
      return { ...state, error: payload };
    //상세주소 작성
    case types.SET_DETAIL_ADDRESS:
      return { ...state, detailAddress: payload };
    case types.SET_DETAIL_ADDRESS_SUCCESS:
      return { ...state, detailAddress: payload };
    case types.SET_DETAIL_ADDRESS_FAIL:
      return { ...state, error: payload };
    // 아파트 설명 설정
    case types.SET_DESCRIPTION:
      return { ...state, description: payload };
    case types.GET_DESCRIPTION_SUCCESS:
      return { ...state, description: payload };
    // 아파트 거래 방법 설정
    case types.SET_TRANSACTION_METHOD:
      return { ...state, transactionMethod: payload };
    case types.GET_TRANSACTION_METHOD_SUCCESS:
      return { ...state, transactionMethod: payload };

    // apartments 배열에 photos 추가
    case types.SET_APARTMENT_PHOTOS:
      return { ...state, apartments: [...state.apartments, ...payload] };
    case types.SET_APARTMENT_PHOTOS_SUCCESS:
      return { ...state, apartments: [...state.apartments, ...payload] };
    case types.SET_APARTMENT_PHOTOS_FAIL:
      return { ...state, error: payload };
    // direction 아파트 방향
    case types.SET_DIRECTION_SELECT:
      return { ...state, direction: payload };
    case types.SET_DIRECTION_SELECT_SUCCESS:
      return { ...state, direction: payload };
    case types.SET_DIRECTION_SELECT_FAIL:
      return { ...state, error: payload };
    //prorertyInformation 매물정보
    case types.SET_PROPERTYINFORMATION_SELECT:
      return { ...state, propertyInformation: payload };
    case types.SET_PROPERTYINFORMATION_SELECT_SUCCESS:
      return { ...state, propertyInformation: payload };
    case types.SET_PROPERTYINFORMATION_SELECT_FAIL:
      return { ...state, error: payload };
    //maintenanceFee 관리비 입력
    case types.SET_MAINTENANCEFEE_SELECT:
      return { ...state, maintenanceFee: payload };
    case types.SET_MAINTENANCEFEE_SELECT_SUCCESS:
      return { ...state, maintenanceFee: payload };
    case types.SET_MAINTENANCEFEE_SELECT_FAIL:
      return { ...state, error: payload };
    //propertyDetail: 매물세부정보 가능여부
    case types.SET_PROPERTYDETAIL_SELECT:
      return { ...state, propertyDetail: payload };
    case types.SET_PROPERTYDETAIL_SELECT_SUCCESS:
      return { ...state, propertyDetail: payload };
    case types.SET_PROPERTYDETAIL_SELECT_FAIL:
      return { ...state, error: payload };
    //move_in : 입주날짜 선택
    case types.SET_MOVEIN_SELECT:
      return { ...state, moveIn: payload };
    case types.SET_MOVEIN_SELECT_SUCCESS:
      return { ...state, moveIn: payload };
    case types.SET_MOVEIN_SELECT_FAIL:
      return { ...state, error: payload };
    //집 내부 인테리어 옵션
    case types.SET_INTERIOROPTION_SELECT:
      return { ...state, interiorOption: payload };
    case types.SET_INTERIOROPTION_SELECT_SUCCESS:
      return { ...state, interiorOption: payload };
    case types.SET_INTERIOROPTION_SELECT_FAIL:
      return { ...state, error: payload };
    //매물 장점
    case types.SET_PROPERTYFEATURES_SELECT:
      return { ...state, propertyFeatures: payload };
    case types.SET_PROPERTYFEATURES_SELECT_SUCCESS:
      return { ...state, propertyFeatures: payload };
    case types.SET_PROPERTYFEATURES_SELECT_FAIL:
      return { ...state, error: payload };
    //매물 소개
    case types.SET_INTRODUCTION_SELECT:
      return { ...state, introduction: payload };
    case types.SET_INTRODUCTION_SELECT_SUCCESS:
      return { ...state, introduction: payload };
    case types.SET_INTRODUCTION_SELECT_FAIL:
      return { ...state, error: payload };
    default:
      return state;
  }
}

export default apartmentReducer;
