import React, { useState } from "react";

//fortawesome 부분
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMap, faStamp } from "@fortawesome/free-regular-svg-icons";
import { faUser as solidUser } from "@fortawesome/free-solid-svg-icons";
import { faUser as regularUser } from "@fortawesome/free-regular-svg-icons";
import {} from "@fortawesome/fontawesome-svg-core";
import { faComments } from "@fortawesome/free-regular-svg-icons";
import {
  faStreetView,
  faBars,
  faBox,
  faSearch,
  faShoppingBag,
  faPenNib,
  faHeadset,
  faWrench,
} from "@fortawesome/free-solid-svg-icons";

import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { userActions } from "../action/userAction";
import uaLogo from "../images/UaLogo.png";

//popup
import PopupModal from "./PopupModal";
import Modal from "../component/Modal";
import Construction from "../page/Construction";

const Navbar = ({ user }) => {
  const dispatch = useDispatch();
  const { cartItemCount } = useSelector((state) => state.cart);
  const isMobile = window.navigator.userAgent.indexOf("Mobile") !== -1;
  const [showSearchBox, setShowSearchBox] = useState(false);
  const menuList = ["아파트 검색", "아파트 등록", "직거래 채팅", "광고문의"];
  //popup
  const [modalOpen, setModalOpen] = useState(false);

  const openModal = () => {
    setModalOpen(true);
    console.log("chilk", openModal);
  };
  const closeModal = () => {
    setModalOpen(false);
  };

  let [width, setWidth] = useState(0);
  let navigate = useNavigate();
  const onCheckEnter = (event) => {
    if (event.key === "Enter") {
      if (event.target.value === "") {
        return navigate("/");
      }
      navigate(`?name=${event.target.value}`);
    }
  };
  const logout = () => {
    dispatch(userActions.logout());
  };

  return (
    <div>
      {showSearchBox && (
        <div className="display-space-between mobile-search-box w-100">
          <div className="search display-space-between w-100">
            <div>
              <FontAwesomeIcon className="search-icon" icon={faSearch} />
              <input
                type="text"
                placeholder="제품검색"
                onKeyPress={onCheckEnter}
              />
            </div>
            <button
              className="closebtn"
              onClick={() => setShowSearchBox(false)}>
              &times;
            </button>
          </div>
        </div>
      )}
      <div className="side-menu" style={{ width: width }}>
        <button className="closebtn" onClick={() => setWidth(0)}>
          &times;
        </button>

        <div className="side-menu-list" id="menu-list">
          {menuList.map((menu, index) => (
            <button key={index}>{menu}</button>
          ))}
        </div>
      </div>
      {user && user.level === "admin" && (
        <Link to="/admin/product?page=1" className="link-area">
          Admin page
        </Link>
      )}
      <div className="nav-menu-area">
        <div className="nav-logo">
          <Link to="/">
            <img width={130} src={uaLogo} alt="hm-logo.png" />
          </Link>
        </div>
        <ul className="menu">
          <div className="menu-list">
            /*<Link className="nav-item-list" to="/construction">
              <FontAwesomeIcon icon={faWrench} />
              {!isMobile && (
                <span style={{ fontSize: "17px", cursor: "pointer" }}>
                  개발 진행중
                </span>
              )}
            </Link> */
            <Link
              className="nav-item-list"
              /*  to="/aptsearch" */ onClick={openModal}>
              <FontAwesomeIcon icon={faMap} />
              {!isMobile && (
                <span style={{ fontSize: "17px", cursor: "pointer" }}>
                  아파트 검색
                </span>
              )}
            </Link>
            <Modal open={modalOpen} close={closeModal} header="개발 진행중">
              11월 8일 정식 오픈
            </Modal>
            <Link
              className="nav-item-list"
              /*  to="/apartmentregister" */ onClick={openModal}>
              <FontAwesomeIcon icon={faPenNib} />
              {!isMobile && (
                <span style={{ fontSize: "17px", cursor: "pointer" }}>
                  아파트 등록
                </span>
              )}
            </Link>
            <Link
              className="nav-item-list"
              /* to="/chatpage" */ onClick={openModal}>
              <FontAwesomeIcon icon={faComments} />
              {!isMobile && (
                <span style={{ fontSize: "17px", cursor: "pointer" }}>
                  직거래 채팅
                </span>
              )}
            </Link>
            <Link
              className="nav-item-list"
              /* to="/adinquirypage" */ onClick={openModal}>
              <FontAwesomeIcon icon={faHeadset} />
              {!isMobile && (
                <span style={{ fontSize: "17px", cursor: "pointer" }}>
                  광고문의
                </span>
              )}
            </Link>
          </div>
        </ul>
        <div className="login-button">
          {user ? (
            <div onClick={logout} className="nav-icon">
              <FontAwesomeIcon icon={solidUser} />
              {!isMobile && <span style={{ cursor: "pointer" }}>로그아웃</span>}
            </div>
          ) : (
            <div onClick={() => navigate("/login")} className="nav-icon">
              <FontAwesomeIcon icon={regularUser} />
              {!isMobile && <span style={{ cursor: "pointer" }}>로그인</span>}
            </div>
          )}
        </div>
        {/*  {!isMobile && ( // admin페이지에서 같은 search-box스타일을 쓰고있음 그래서 여기서 서치박스 안보이는것 처리를 해줌
          <div className="search-box landing-search-box ">
            <FontAwesomeIcon icon={faSearch} />
            <input
              type="text"
              placeholder="제품검색"
              onKeyPress={onCheckEnter}
            />
          </div>
        )} */}
      </div>
    </div>
  );
};

export default Navbar;
