import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { setAddress } from "../action/apartmentAction";
import DaumPostcode from "react-daum-postcode";
import Modal from "react-modal";

const Rag04 = () => {
  const [zipCode, setZipcode] = useState("");
  const [roadAddress, setRoadAddress] = useState("");
  const [detailAddress, setDetailAddress] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const dispatch = useDispatch();

  const completeHandler = (data) => {
    setZipcode(data.zonecode);
    setRoadAddress(data.roadAddress);
    setIsOpen(false);
  };
  const customStyles = {
    overlay: {
      backgroundColor: "rgba(0,0,0,0.5)",
    },
    content: {
      left: "0",
      margin: "auto",
      width: "500px",
      height: "600px",
      padding: "0",
      overflow: "hidden",
    },
  };

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  const changeHandler = (e) => {
    setDetailAddress(e.target.value);
  }; 
  const handleDetailAddressChange = (event) => {
    setDetailAddress(event.target.value);
  };

  const clickHandler = () => {
    if (detailAddress === "") {
      alert("상세 주소를 입력하세요.");
    } else {
      /* console.log(zipCode, roadAddress, detailAddress);
      dispatch(setAddress(roadAddress)); // setAddress 액션을 디스패치합니다
      dispatch(setDetailAddress(detailAddress)); // setDetailAddress 액션을 디스패치합니다 */
      const fullAddress ={
        zipCode,
        roadAddress,
        detailAddress,
      }
      dispatch(setAddress(fullAddress));
    }
  };

  return (
    <div>
      <h1>Rag04</h1>
      <input value={zipCode} readOnly placeholder="우편번호" />
      <button onClick={toggle}>우편번호 검색</button>
      <br />
      <input value={roadAddress} readOnly placeholder="도로명 주소" />
      <br />
      <Modal isOpen={isOpen} ariaHideApp={false} style={customStyles}>
        <DaumPostcode onComplete={completeHandler} height="100%" />
      </Modal>
      <input
        type="text"
        onChange={handleDetailAddressChange}
        value={detailAddress}
        placeholder="상세주소"
      />
      <br />
      <button onClick={clickHandler}>클릭</button>
    </div>
  );
};

export default Rag04;
