import React, { useState } from "react";
import { Container, Button } from "react-bootstrap";

import Rag01 from "./Rag01";
import Rag02 from "./Rag02";
import Rag03 from "./Rag03";
import Rag04 from "./Rag04";
import Rag05 from "./Rag05";
import Rag06 from "./Rag06";
import Rag07 from "./Rag07";
import Rag08 from "./Rag08";
import Rag09 from "./Rag09";
import Rag10 from "./Rag10";
import Rag11 from "./Rag11";
import Rag12 from "./Rag12";
import Rag13 from "./Rag13";
import Rag14 from "./Rag14";
import Rag15 from "./Rag15";
import Rag16 from "./Rag16";

const AptRegistrationPage = () => {
  const pages = [Rag01,Rag02,Rag03,Rag04,Rag05,Rag06,Rag07,Rag08,Rag09,Rag10,Rag11,Rag12,Rag13,Rag14,Rag15,Rag16,];
  const [currentPage, setCurrentPage] = useState(0);


  const nextPage = () => {
    if (currentPage < pages.length - 1) {
      setCurrentPage(currentPage + 1);
    }
  };
  const prevPage = () => {
    if (currentPage > 0) {
      setCurrentPage(currentPage - 1);
    }
  };
  const CurrentPageComponent = pages[currentPage];
  
  //등록페이지 생성
  const handleClickNewApartment =()=>{

  }
  //등록페이지 수정

  //다이얼로그 열어주기
  return (
    <div>
      <h1>아파트 등록</h1>
      <div className="apt-reg-page">
        <CurrentPageComponent />
      </div>
      <div>
      <Button className="mt-2 mb-2" onClick={handleClickNewApartment}>
          Add New Item +
        </Button>
      </div>
      <div>
        <button onClick={prevPage} disabled={currentPage === 0}>
          이전
        </button>
        <button onClick={nextPage} disabled={currentPage === pages.length - 1}>
          다음
        </button>
      </div>
    </div>
  );
};

export default AptRegistrationPage;
