import React, { useState } from "react";
import DatePicker from "react-datepicker";
import { useDispatch } from "react-redux";
import { saveMoveIn } from "../action/apartmentAction";
import { ko } from "date-fns/esm/locale";
import "react-datepicker/dist/react-datepicker.css";
import Form from "react-bootstrap/Form";

const Rag12 = () => {
  //const [startDate, setStartDate] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [isChecked, setIsChecked] = useState(false);

  const dispatch = useDispatch();

  const handleSaveMoveIn = () => {
    // 액션에 보낼 데이터 준비
    // Date 객첼르 직렬화 가능한 문자열로 변환
    const moveInSelect = {
      date: selectedDate.toISOString(), //Date 객첼르 ISO 문자열로 변환
      바로입주가능: isChecked, //availableImmediately <- 바로 입주 가능 영어로
    };
    // 액션 디스패치
    dispatch(saveMoveIn(moveInSelect));
  };
  return (
    <div className="ragBody">
      <h1>Rag12</h1>
      <div className="DateP">
        <h2>입주 가능한 날짜를 선택해주세요</h2>
        <DatePicker
          selected={selectedDate}
          onChange={(date) => setSelectedDate(date)}
          showPopperArrow={false}
          inline
          locale={ko}
          placeholderText="주의: 주의 시작일"
          minDate={new Date()}
        />
        <Form>
          <div className="mb-3">
            <Form.Check
              type="checkbox"
              id={`check-api`}
              checked={isChecked}
              onChange={() => setIsChecked(!isChecked)}
            />
            <Form.Check.Label>바로 입주 가능합니다.</Form.Check.Label>
          </div>
        </Form>
        <button onClick={handleSaveMoveIn}>입주 날짜 저장</button>
      </div>
    </div>
  );
};

export default Rag12;
