import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { setInternalSize } from "../action/apartmentAction";
import "../styles/buttonStyle.css"

const Rag03 = () => {
  const dispatch = useDispatch();
  const selectedInternalSize = useSelector(
    (state) => state.apartment.selectedInternalSize
  );

  const handleRadioChange = (event) => {
    const internalSize = event.target.id;
    dispatch(setInternalSize(internalSize));
  };
  return (
    <div className="ragBody">
      <h1>Rag03</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>아파트 평수를 선택해주세요</h1>
        </div>
      </div>
      <div className="home_rgt_content_space">
        <div className="rgt3_main">
          <div className="p_exp">
            <h4>공급면적(평형) 기준:</h4>
          </div>

          <div className="hom_sizePick">
            <ul>
              <li>
                <input
                  type="radio"
                  name="hom_size"
                  id="10pyeong"
                  checked={selectedInternalSize === "10pyeong"}
                  onChange={handleRadioChange}
                />
                <label htmlFor="10pyeong">
                  <span>10평</span>
                  <span className="hom_size_ex">(33㎡)</span>
                </label>
              </li>
              <li>
                <input
                  type="radio"
                  name="hom_size"
                  id="20pyeong"
                  checked={selectedInternalSize === "20pyeong"}
                  onChange={handleRadioChange}
                />
                <label htmlFor="20pyeong">
                  <span>20평</span>
                  <span className="hom_size_ex">(66㎡)</span>
                </label>
              </li>
              <li>
                <input
                  type="radio"
                  name="hom_size"
                  id="30pyeong"
                  checked={selectedInternalSize === "30pyeong"}
                  onChange={handleRadioChange}
                />
                <label htmlFor="30pyeong">
                  <span>30평</span>
                  <span className="hom_size_ex">(99㎡)</span>
                </label>
              </li>
              <li>
                <input
                  type="radio"
                  name="hom_size"
                  id="40pyeong"
                  checked={selectedInternalSize === "40pyeong"}
                  onChange={handleRadioChange}
                />
                <label htmlFor="40pyeong">
                  <span>40평</span>
                  <span className="hom_size_ex">(132㎡)</span>
                </label>
              </li>
              <li>
                <input
                  type="radio"
                  name="hom_size"
                  id="50pyeong"
                  checked={selectedInternalSize === "50pyeong"}
                  onChange={handleRadioChange}
                />
                <label htmlFor="50pyeong">
                  <span>50평</span>
                  <span className="hom_size_ex">(165㎡) </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Rag03;
