import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { saveDirectionSelect } from "../action/apartmentAction";

const Rag08 = () => {
  const dispatch = useDispatch();
  const directionSelect = useSelector(
    (state) => state.apartment.directionSelect
  );

  const handleDirectionChange = (event) => {
    const directionSelect = event.target.id;
    dispatch(saveDirectionSelect(directionSelect));
  };

  return (
    <div className="ragBody">
      <h1>Rag08</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>매물의 방향을 선택해주세요.</h1>
          <p>거실 베란다 또는 큰 창문의 방향이 기준이에요.</p>
        </div>
      </div>
      <div className="home_rgt_content_space">
        <div className="direction_pick">
          <table>
            <tbody>
              <tr>
                <th>
                  <div className="nw">
                    <input
                      type="radio"
                      name="hom_size"
                      id="nw"
                      checked={directionSelect === "nw"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="nw">
                      <span>북서향</span>
                    </label>
                  </div>
                </th>
                <th>
                  <div className="nt">
                    <input
                      type="radio"
                      name="hom_size"
                      id="nt"
                      checked={directionSelect === "nt"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="nt">
                      <span>북향</span>
                    </label>
                  </div>
                </th>
                <th>
                  <div className="ne">
                    <input
                      type="radio"
                      name="hom_size"
                      id="ne"
                      checked={directionSelect === "ne"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="ne">
                      <span>북동향</span>
                    </label>
                  </div>
                </th>
              </tr>
              <tr>
                <th>
                  <div className="ws">
                    <input
                      type="radio"
                      name="hom_size"
                      id="ws"
                      checked={directionSelect === "ws"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="ws">
                      <span>서향</span>
                    </label>
                  </div>
                </th>
                <th>
                  <div className="middle">
                    <input
                      type="radio"
                      name="hom_size"
                      id="dummy"
                      checked={directionSelect === "dummy"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="dummy">
                      <span>+</span>
                    </label>
                  </div>
                </th>
                <th>
                  <div className="es">
                    <input
                      type="radio"
                      name="hom_size"
                      id="es"
                      checked={directionSelect === "es"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="es">
                      <span>동향</span>
                    </label>
                  </div>
                </th>
              </tr>
              <tr>
                <th>
                  <div className="sw">
                    <input
                      type="radio"
                      name="hom_size"
                      id="sw"
                      checked={directionSelect === "sw"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="sw">
                      <span>남서향</span>
                    </label>
                  </div>
                </th>
                <th>
                  <div className="st">
                    <input
                      type="radio"
                      name="hom_size"
                      id="st"
                      checked={directionSelect === "st"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="st">
                      <span>남향</span>
                    </label>
                  </div>
                </th>
                <th>
                  <div className="se">
                    <input
                      type="radio"
                      name="hom_size"
                      id="se"
                      checked={directionSelect === "se"}
                      onChange={handleDirectionChange}
                    />
                    <label htmlFor="se">
                      <span>남동향</span>
                    </label>
                  </div>
                </th>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="p2_main2_img"></div>
      </div>
    </div>
  );
};

export default Rag08;
