// 모든 페이지 설정
import React from "react";
import { Route, Routes } from "react-router";
import AdminOrderPage from "../page/AdminOrderPage";
import AdminProduct from "../page/AdminProduct";
import CartPage from "../page/CartPage";
import Login from "../page/Login";
import MyPage from "../page/MyPage";
import OrderCompletePage from "../page/OrderCompletePage";
import PaymentPage from "../page/PaymentPage";
import ProductAll from "../page/ProductAll";
import ProductDetail from "../page/ProductDetail";
import RegisterPage from "../page/RegisterPage";
import PrivateRoute from "../Route/PrivateRoute";

//page 정보
import AptSearchPage from "../pages-test/AptSearchPage";
import AptRegistrationPage from "../pages-test/AptRegistrationPage";
import ChatPage from "../pages-test/ChatPage";
import AdInquiryPage from "../pages-test/AdInquiryPage";
import KakaoMap from "../component/KakaoMap";
//metaverse 체험 페이지
import MetaPage from "../page/MetaPage";
import MetaWorld01 from "../pages-test/MetaWorld01";
import MetaWorld02 from "../pages-test/MetaWorld02";
import MetaWorld03 from "../pages-test/MetaWorld03";
import MetaWorld04 from "../pages-test/MetaWorld04";
import MetaWorld05 from "../pages-test/MetaWorld05";

import Construction from "../page/Construction";
import GameDownload from "../page/GameDownload";

const AppRouter = () => {
  return (
    <Routes>
      <Route path="/" element={<ProductAll />} />
      <Route path="/login" element={<Login />} />
      <Route path="/register" element={<RegisterPage />} />
      <Route path="/product/:id" element={<ProductDetail />} />
      <Route element={<PrivateRoute permissionLevel="customer" />}>
        <Route path="/cart" element={<CartPage />} />
        <Route path="/payment" element={<PaymentPage />} />
        <Route path="/payment/success" element={<OrderCompletePage />} />
        <Route path="/account/purchase" element={<MyPage />} />
      </Route>
      <Route element={<PrivateRoute permissionLevel="admin" />}>
        <Route path="/admin/product" element={<AdminProduct />} />
        <Route path="/admin/order" element={<AdminOrderPage />} />
      </Route>
      <Route element={<PrivateRoute permissionLevel="admin" />}>
        <Route path="/aptsearch" element={<AptSearchPage />} />
        <Route path="/apartmentregister" element={<AptRegistrationPage />} />
        <Route path="/chatpage" element={<ChatPage />} />
        <Route path="/adinquirypage" element={<AdInquiryPage />} />
        <Route path="/kakaoMap" element={<KakaoMap />} />
      </Route>
      <Route element={<PrivateRoute permissionLevel="custmoer" />}>
        <Route path="/metapage" element={<MetaPage />} />
        <Route path="/metpage/10pyeong" element={<MetaWorld01 />} />
        <Route path="/metpage/20pyeong" element={<MetaWorld02 />} />
        <Route path="/metpage/30pyeong" element={<MetaWorld03 />} />
        <Route path="/metpage/40pyeong" element={<MetaWorld04 />} />
        <Route path="/metpage/50pyeong" element={<MetaWorld05 />} />
      </Route>
      <Route path="/construction" element={<Construction/>}/>
      <Route path="/gamedownload" element={<GameDownload/>}/>
    </Routes>
  );
};

export default AppRouter;
