import api from "../utils/api";
import * as types from "../constants/cart.constants";
import { commonUiActions } from "../action/commonUiAction";

// addToCart 액션 생성자 함수는 카트에 항목을 추가합니다.
const addToCart =
  ({ id, size }) =>
  async (dispatch) => {
    try {
      // 카트에 항목 추가 요청 시작을 알립니다.
      dispatch({ type: types.ADD_TO_CART_REQUEST }); 
      // 카트에 항목을 추가하기 위해 API를 호출합니다.
      const response = await api.post("/cart", { size, productId: id, qty: 1 });

      if (response.status !== 200) throw new Error(response.error);
      // 항목이 성공적으로 추가되면 카트 상태를 업데이트하고 메시지를 표시합니다.
      dispatch({
        type: types.ADD_TO_CART_SUCCESS,
        payload: response.data.cartItemQty,
      });
      dispatch(
        commonUiActions.showToastMessage(
          "카트에 아이템이 추가됐습니다!",
          "success"
        )
      );
    } catch (error) {
      // 에러 발생 시 에러 상태를 업데이트하고 에러 메시지를 표시합니다.
      dispatch({ type: types.ADD_TO_CART_FAIL, payload: error.error });
      dispatch(commonUiActions.showToastMessage(error.error, "error"));
    }
  };
// getCartList 액션 생성자 함수는 카트에 있는 항목 목록을 가져옵니다.
const getCartList = () => async (dispatch) => {
  try {
    // 카트 목록 가져오기 요청 시작을 알립니다.
    dispatch({ type: types.GET_CART_LIST_REQUEST });
    // 카트 목록을 가져오기 위해 API를 호출합니다.
    const response = await api.get("/cart");
    if (response.status !== 200) throw new Error(response.error);
    // 카트 목록을 성공적으로 가져오면 카트 상태를 업데이트합니다.
    dispatch({
      type: types.GET_CART_LIST_SUCCESS,
      payload: response.data.data,
    });
  } catch (error) {
    // 에러 발생 시 에러 상태를 업데이트하고 에러 메시지를 표시합니다.
    dispatch({ type: types.GET_CART_LIST_FAIL, payload: error });
    dispatch(commonUiActions.showToastMessage(error, "error"));
  }
};
// deleteCartItem 액션 생성자 함수는 카트에서 항목을 삭제합니다.
const deleteCartItem = (id) => async (dispatch) => {
  try {
    // 카트 항목 삭제 요청 시작을 알립니다.
    dispatch({ type: types.DELETE_CART_ITEM_REQUEST });
    const response = await api.delete(`/cart/${id}`);
    if (response.status !== 200) throw new Error(response.error);
    // 항목이 성공적으로 삭제되면 카트 상태를 업데이트하고 카트 목록을 다시 가져옵니다.
    dispatch({
      type: types.DELETE_CART_ITEM_SUCCESS,
      payload: response.data.cartItemQty,
    });
    // 카트 목록을 다시 가져오는 액션을 호출합니다.
    dispatch(getCartList());
  } catch (error) {
    // 에러 발생 시 에러 상태를 업데이트하고 에러 메시지를 표시합니다.
    dispatch({ type: types.DELETE_CART_ITEM_FAIL, payload: error });
    dispatch(commonUiActions.showToastMessage(error, "error"));
  }
};
// updateQty 액션 생성자 함수는 카트 항목의 수량을 업데이트합니다.
const updateQty = (id, value) => async (dispatch) => {
  try {
    // 카트 항목 업데이트 요청 시작을 알립니다.
    dispatch({ type: types.UPDATE_CART_ITEM_REQUEST });
    const response = await api.put(`/cart/${id}`, { qty: value });
    if (response.status !== 200) throw new Error(response.error);
    // 항목이 성공적으로 업데이트되면 카트 상태를 업데이트합니다.
    dispatch({
      type: types.UPDATE_CART_ITEM_SUCCESS,
      payload: response.data.data,
    });
  } catch (error) {
    // 에러 발생 시 에러 상태를 업데이트하고 에러 메시지를 표시합니다.
    dispatch({ type: types.UPDATE_CART_ITEM_FAIL, payload: error });
    dispatch(commonUiActions.showToastMessage(error, "error"));
  }
};
// getCartQty 액션 생성자 함수는 카트에 있는 항목 수량을 가져옵니다.
const getCartQty = () => async (dispatch) => {
  try {
    // 카트 항목 수량 가져오기 요청 시작을 알립니다.
    dispatch({ type: types.GET_CART_QTY_REQUEST });
    // 카트 항목 수량을 가져오기 위해 API를 호출합니다.
    const response = await api.get("/cart/qty");
    if (response.status !== 200) throw new Error(response.error);
    // 카트 항목 수량을 성공적으로 가져오면 상태를 업데이트합니다.
    dispatch({ type: types.GET_CART_QTY_SUCCESS, payload: response.data.qty });
  } catch (error) {
    // 에러 발생 시 에러 상태를 업데이트하고 에러 메시지를 표시합니다.
    dispatch({ type: types.GET_CART_QTY_FAIL, payload: error });
    dispatch(commonUiActions.showToastMessage(error, "error"));
  }
};
// 모든 카트 액션 생성자 함수를 내보냅니다.
export const cartActions = {
  addToCart,
  getCartList,
  deleteCartItem,
  updateQty,
  getCartQty,
};
