import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { setUserType } from "../action/apartmentAction";

const Rag02 = () => {
  const dispatch = useDispatch();
  const [isChecked, setIsChecked] = useState(false);

  const handleCheckChange = async (event) => {
    const selectedUserType = event.target.id;
    setIsChecked(selectedUserType === "tenant");

    const userTypeText = selectedUserType === "tenant" ? "세입자" : "집주인";
    console.log(`글작성자: ${userTypeText}`);

    // Redux 액션을 디스패치하여 사용자 유형 저장
    dispatch(setUserType(selectedUserType));
  
  };

  return (
    <div className="ragBody">
      <h1>Rag02</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>누가 글을 작성하나요?</h1>
        </div>
      </div>
      <div className="rgt2_main">
        <li>
          <input
            type="radio"
            id="tenant"
            checked={isChecked}
            onChange={handleCheckChange}
          />
          <label htmlFor="tenant">세입자</label>
        </li>
        <li>
          <input
            type="radio"
            id="homeowner"
            checked={!isChecked}
            onChange={handleCheckChange}
          />
          <label htmlFor="homeowner">집주인</label>
        </li>
      </div>
    </div>
  );
};

export default Rag02;
