import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { saveApartmentPhotos } from "../action/apartmentAction";

const Rag07 = () => {
  const dispatch = useDispatch();
  const [file1, setFile1] = useState("");
  const [file2, setFile2] = useState("");
  const [file3, setFile3] = useState("");
  const [file1Preview, setFile1Preview] = useState(null);
  const [file2Preview, setFile2Preview] = useState(null);
  const [file3Preview, setFile3Preview] = useState(null);

  const previewSelectedFile = (file, previewSetter) => {
    const reader = new FileReader();

    reader.onload = (e) => {
      previewSetter(e.target.result);
    };
    reader.readAsDataURL(file);
  };

  const handleFileChange = (event, setter, previewSetter) => {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      setter(selectedFile.name);
      previewSelectedFile(selectedFile, previewSetter);
    }
  };
  const handleUpload = () => {
    const photos = [file1, file2, file3].filter((fileName) => !!fileName);
    dispatch(saveApartmentPhotos(photos));
  };

  return (
    <div className="ragBody">
      <h1>Rag07</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>매물을 잘 드러내는 사진을</h1>
          <h1>올려주세요</h1>
        </div>
      </div>
      <div className="home_rgt_content_space">
        <div className="photo_upload1">
          <ul>
            <li>
              <div className="uploadexp1">
                <h4>매물 사진(3장 이상)</h4>
                <p>최대 20장까지 올릴 수 있어요.</p>
                <div>
                  <input
                    type="text"
                    id="fileName1"
                    className="file_input_textbox"
                    readOnly
                    value={file1}
                  />
                  <div className="file_input_div">
                    <input
                      type="file"
                      name="file_1"
                      className="file_input_hidden"
                      onChange={(e) =>
                        handleFileChange(e, setFile1, setFile1Preview)
                      }
                    />
                    <label className="file_input_label">등록</label>
                  </div>
                  <div className="photos_container">
                    {file1Preview && (
                      <img
                        src={file1Preview}
                        className="file_input_preview_img"
                        alt="Preview 1"
                      />
                    )}
                  </div>
                </div>
              </div>
            </li>
            <li>
              <div className="uploadexp2">
                <h4>창박의 뷰 (선택)</h4>
                <p>최대 10장까지 올릴 수 있어요.</p>
                <div>
                  <input
                    type="text"
                    id="fileName2"
                    className="file_input_textbox"
                    readOnly
                    value={file2}
                  />
                  <div className="file_input_div">
                    {file2Preview && (
                      <img
                        src={file2Preview}
                        className="file_input_preview_img"
                        alt="Preview 2"
                      />
                    )}
                    <input
                      type="file"
                      name="file_2"
                      className="file_input_hidden"
                      onChange={(e) =>
                        handleFileChange(e, setFile2, setFile2Preview)
                      }
                    />
                    <label className="file_input_label">등록</label>
                  </div>
                  <div className="photos_container">
                    {file2Preview && (
                      <img
                        src={file2Preview}
                        className="file_input_preview_img"
                        alt="Preview 2"
                      />
                    )}
                  </div>
                </div>
              </div>
            </li>
            <li>
              <div className="uploadexp3">
                <h4>평면도 사진 (선택)</h4>
                <p>최대 3장까지 올릴 수 있어요.</p>
                <div>
                  <input
                    type="text"
                    id="fileName3"
                    className="file_input_textbox"
                    readOnly
                    value={file3}
                  />
                  <div className="file_input_div">
                    <input
                      type="file"
                      name="file_3"
                      className="file_input_hidden"
                      onChange={(e) =>
                        handleFileChange(e, setFile3, setFile3Preview)
                      }
                    />
                    <label className="file_input_label">등록</label>
                  </div>
                  <div className="photos_container">
                    {file3Preview && (
                      <img
                        src={file3Preview}
                        className="file_input_preview_img"
                        alt="Preview 3"
                      />
                    )}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <button onClick={handleUpload}>사진 등록</button>
    </div>
  );
};

export default Rag07;
