import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { savePropertyDetail } from "../action/apartmentAction";

const Rag11 = () => {
  const dispatch = useDispatch();
  const { loan, pet, parking } = useSelector(
    (state) => state.apartment.propertyDetail
  );

  // 변경된 값을 저장할 상태 변수
  const [updatedLoan, setUpdatedLoan] = useState(loan);
  const [updatedPet, setUpdatedPet] = useState(pet);
  const [updatedParking, setUpdatedParking] = useState(parking);

  // 라디오 버튼 변경 핸들러
  const handleOptionChange = (optionType, optionValue) => {
    if (optionType === "loan") {
      setUpdatedLoan(optionValue);
    } else if (optionType === "pet") {
      setUpdatedPet(optionValue);
    } else if (optionType === "parking") {
      setUpdatedParking(optionValue);
    }
  };

  // 선택 완료 버튼 클릭 핸들러
  const handleSaveChanges = () => {
    // 변경된 값을 Redux store에 저장
    const updatedPropertyDetail = {
      loan: updatedLoan,
      pet: updatedPet,
      parking: updatedParking,
    };
    dispatch(savePropertyDetail(updatedPropertyDetail));
  };

  return (
    <div className="ragBody">
      <h1>Rag11</h1>
      <div className="category_pick">
        <ul>
          <div className="category_pick">
            <li>
              <div className="loan_posAndimpos">
                <h4>대출</h4>
                <label>
                  <input
                    type="radio"
                    value="가능"
                    checked={loan === "가능"}
                    onChange={() => handleOptionChange("loan", "가능")}
                  />
                  가능
                </label>
                <label>
                  <input
                    type="radio"
                    value="불가능"
                    checked={loan === "불가능"}
                    onChange={() => handleOptionChange("loan", "불가능")}
                  />
                  불가능
                </label>
                <label>
                  <input
                    type="radio"
                    value="확인 필요"
                    checked={loan === "확인 필요"}
                    onChange={() => handleOptionChange("loan", "확인 필요")}
                  />
                  확인 필요
                </label>
              </div>
            </li>
          </div>
          <div className="category_pick">
            <li>
              <div className="pet_posAndimpos">
                <h4>반려동물</h4>
                <label>
                  <input
                    type="radio"
                    value="가능"
                    checked={pet === "가능"}
                    onChange={() => handleOptionChange("pet", "가능")}
                  />
                  가능
                </label>
                <label>
                  <input
                    type="radio"
                    value="불가능"
                    checked={pet === "불가능"}
                    onChange={() => handleOptionChange("pet", "불가능")}
                  />
                  불가능
                </label>
                <label>
                  <input
                    type="radio"
                    value="확인 필요"
                    checked={pet === "확인 필요"}
                    onChange={() => handleOptionChange("pet", "확인 필요")}
                  />
                  확인 필요
                </label>
              </div>
            </li>
          </div>
          <div className="category_pick">
            <li>
              <div className="parking_posAndimpos">
                <h4>주차</h4>
                <label>
                  <input
                    type="radio"
                    value="가능"
                    checked={parking === "가능"}
                    onChange={() => handleOptionChange("parking", "가능")}
                  />
                  가능
                </label>
                <label>
                  <input
                    type="radio"
                    value="불가능"
                    checked={parking === "불가능"}
                    onChange={() => handleOptionChange("parking", "불가능")}
                  />
                  불가능
                </label>
                <label>
                  <input
                    type="radio"
                    value="확인 필요"
                    checked={parking === "확인 필요"}
                    onChange={() => handleOptionChange("parking", "확인 필요")}
                  />
                  확인 필요
                </label>
              </div>
            </li>
          </div>
        </ul>
        <button onClick={handleSaveChanges}>선택 완료</button>
      </div>
    </div>
  );
};

export default Rag11;
