import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { savePropertyInformation } from "../action/apartmentAction";

const Rag09 = () => {
  const dispatch = useDispatch();

  // 입력 필드의 값을 저장할 상태 변수들을 정의
  const [squareMeter, setSquareMeter] = useState(0);
  const [roomCount, setRoomCount] = useState("");
  const [bathroomCount, setBathroomCount] = useState("");
  const [totalFloor, setTotalFloor] = useState("");
  const [currentFloor, setCurrentFloor] = useState("");

  const handleSavePropertyInformation = () => {
    dispatch(
      savePropertyInformation({
        squareMeter,
        roomCount,
        bathroomCount,
        totalFloor,
        currentFloor,
        //maintenance,
      })
    );
  };

  // 입력 필드 값이 변경될 때 상태 업데이트 함수들
  const handleSquareMeterChange = (event) => {
    setSquareMeter(event.target.value);
  };

  const handleRoomCountChange = (event) => {
    setRoomCount(event.target.value);
  };

  const handleBathroomCountChange = (event) => {
    setBathroomCount(event.target.value);
  };

  const handleTotalFloorChange = (event) => {
    setTotalFloor(event.target.value);
  };

  const handleCurrentFloorChange = (event) => {
    setCurrentFloor(event.target.value);
  };

  return (
    <div className="ragBody">
      <h1>Rag09</h1>

      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>거의 다 왔어요,</h1>
          <h1>매물 정보를 입력해주세요</h1>
        </div>
      </div>
      <div className="home_rgt_content_space">
        <div className="main_content">
          <div className="lines">
            <div className="linesOne">
              <div className="hom_flat">
                <input
                  type="number"
                  value={squareMeter}
                  onChange={handleSquareMeterChange}
                  autoFocus
                  required
                  min="0"
                />
                <div className="hom_flat_copyText">
                  <p>평</p>
                </div>
              </div>
              <div className="tenchk">
                <button type="button" id="chgSquaremeter">
                  제곱미터로 입력
                </button>
              </div>
            </div>
            <div className="linesTwo">
              <div className="hom_flat_composition">
                <ul>
                  <li>
                    <h4>방 개수</h4>
                    <input
                      type="number"
                      value={roomCount}
                      onChange={handleRoomCountChange}
                      placeholder="개"
                      min="0"
                    />
                  </li>
                  <li>
                    <h4>욕실 개수</h4>
                    <input
                      type="number"
                      value={bathroomCount}
                      onChange={handleBathroomCountChange}
                      placeholder="개"
                      min="0"
                    />
                  </li>
                  <li>
                    <h4>건물 전체 층</h4>
                    <input
                      type="number"
                      value={totalFloor}
                      onChange={handleTotalFloorChange}
                      placeholder="층"
                      min="0"
                    />
                  </li>
                  <li>
                    <h4>해당 층</h4>
                    <input
                      type="number"
                      value={currentFloor}
                      onChange={handleCurrentFloorChange}
                      placeholder="층"
                      min="0"
                    />
                  </li>
                  {/*  <li>
                    <div className="tenchk">
                      <input
                        type="checkbox"
                        id="maintenance"
                        checked={maintenance}
                        onChange={handleMaintenanceChange}
                      />
                      <label htmlFor="maintenance">반지하</label>
                    </div>
                  </li> */}
                </ul>
              </div>
            </div>
          </div>
        </div>
        <button type="button" onClick={handleSavePropertyInformation}>
          매물 정보 저장
        </button>
        <div className="p2_main2_img"></div>
      </div>
    </div>
  );
};

export default Rag09;
