import React, { useState } from "react";
import "../stylesTest/60,61,64.css";
import { useDispatch } from "react-redux";
import { saveMaintenanceFee } from "../action/apartmentAction"; // 액션을 가져옵니다.

const Rag10 = () => {
  //관리비 입력
  const [maintenanceValue, setMaintenanceValue] = useState("");
  //체크박스: 관리비 없음
  const [maintenance, setMaintenance] = useState(false);
  //관리비 설명
  const [feeExplanation, setFeeExplanation] = useState("");
  const [includedButtonStates, setIncludedButtonStates] = useState({
    수수료: false,
    전기료: false,
    인터넷비: false,
    가스비: false,
    청소비: false,
    유선TV: false,
    주차비: false,
    난방비: false,
    승강기유지비: false,
  });
  const [excludedButtonStates, setExcludedButtonStates] = useState({
    수수료: false,
    전기료: false,
    인터넷비: false,
    가스비: false,
    청소비: false,
    유선TV: false,
    주차비: false,
    난방비: false,
  });

  const handleMaintenanceChange = (event) => {
    setFeeExplanation(event.target.value);
  };
  const handleMaintenanceInputChange = (event) => {
    setMaintenanceValue(event.target.value);
  };

  const dispatch = useDispatch(); // 디스패치 함수를 초기화합니다.
  const handleNoMaintenanceChange = (event) => {
    setMaintenance(event.target.checked);
  };

  const handleButtonClick = (itemName, isIncluded) => {
    if (isIncluded) {
      const updatedIncludedButtonStates = { ...includedButtonStates };
      updatedIncludedButtonStates[itemName] = !includedButtonStates[itemName];
      setIncludedButtonStates(updatedIncludedButtonStates);
      console.log("버튼클릭?", itemName);
    } else {
      const updatedExcludedButtonStates = { ...excludedButtonStates };
      updatedExcludedButtonStates[itemName] = !excludedButtonStates[itemName];
      setExcludedButtonStates(updatedExcludedButtonStates);
    }
  };

  /* const getButtonClass = (itemName, isIncluded) => {
    return isIncluded
      ? includedButtonStates[itemName]
        ? "active clicked"
        : ""
      : separateButtonStates[itemName]
      ? "active clicked"
      : "";
  }; */
  const handleSaveMaintenanceFee = () => {
    // 액션에 보낼 페이로드를 생성합니다.
    const maintenanceFeeSelect = {
      maintenanceValue,
      maintenance,
      includedButtonStates,
      excludedButtonStates,
      feeExplanation,
    };
    // 페이로드와 함께 액션을 디스패치합니다.
    dispatch(saveMaintenanceFee(maintenanceFeeSelect));
  };

  return (
    <div className="ragBody">
      <h1>Rag10</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>관리비를 입력해주세요</h1>
        </div>
      </div>
      <div className="main1">
        <div className="inform_enter">
          <input
            type="text"
            placeholder="만원"
            autoFocus
            required
            value={maintenanceValue}
            onChange={handleMaintenanceInputChange}
          />
        </div>

        <div className="tenchk">
          <input
            type="checkbox"
            id="maintenance"
            checked={maintenance}
            onChange={handleNoMaintenanceChange}
          />
          <label htmlFor="maintenance">관리비 없음</label>
        </div>
      </div>

      <div className="main2">
        <div className="title">
          <h3>관리비에 포함된 항목</h3>
        </div>

        <div className="maintenance_list">
          <ul>
            <div className="row1">
              {Object.keys(includedButtonStates)
                .slice(0, 4)
                .map((itemName) => (
                  <li key={itemName}>
                    <button
                      type="button"
                      onClick={() => handleButtonClick(itemName, true)}>
                      {itemName}
                    </button>
                  </li>
                ))}
            </div>
            <div className="row2">
              {Object.keys(includedButtonStates)
                .slice(4, 8)
                .map((itemName) => (
                  <li key={itemName}>
                    <button
                      type="button"
                      onClick={() => handleButtonClick(itemName, true)}>
                      {itemName}
                    </button>
                  </li>
                ))}
            </div>
            <div className="row3">
              <li>
                <button
                  type="button"
                  onClick={() => handleButtonClick("승강기유지비", true)}>
                  승강기 유지비
                </button>
              </li>
            </div>
          </ul>
        </div>
      </div>
      <div className="main3">
        <div className="title">
          <h4>관리비와 별도인 항목</h4>
        </div>
        <div className="maintenance_list">
          <ul>
            <div className="row1">
              {Object.keys(excludedButtonStates)
                .slice(0, 4)
                .map((itemName) => (
                  <li key={itemName}>
                    <button
                      type="button"
                      onClick={() => handleButtonClick(itemName, true)}>
                      {itemName}
                    </button>
                  </li>
                ))}
            </div>
            <div className="row2">
              {Object.keys(excludedButtonStates)
                .slice(4, 8)
                .map((itemName) => (
                  <li key={itemName}>
                    <button
                      type="button"
                      onClick={() => handleButtonClick(itemName, true)}>
                      {itemName}
                    </button>
                  </li>
                ))}
            </div>
          </ul>
        </div>
      </div>
      <div className="main4">
        <div className="title">
          <h4>관리비 설명</h4>
        </div>
        <div className="inform_explanation">
          <input
            type="text"
            placeholder="예) 여름철엔 5~7만원 정도 나와요"
            value={feeExplanation}
            onChange={handleMaintenanceChange}
          />
        </div>
      </div>
      <button type="button" onClick={handleSaveMaintenanceFee}>
        관리비 저장
      </button>
    </div>
  );
};

export default Rag10;
