import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import { useDispatch } from "react-redux";
import { saveInteriorOption } from "../action/apartmentAction";

const Rag13 = () => {
  const [selectedOptions, setSelectedOptions] = useState([]);
  const dispatch = useDispatch();

  const handleCheckboxChange = (option) => {
    if (selectedOptions.includes(option)) {
      setSelectedOptions(selectedOptions.filter((item) => item !== option));
    } else {
      setSelectedOptions([...selectedOptions, option]);
    }
  };

  const handleSaveOptions = () => {
    dispatch(saveInteriorOption(selectedOptions));
  };

  return (
    <div className="ragBody">
      <h1>Rag13</h1>
      <div className="main-01">
        <h2>보유한 옵션을 선택해주세요</h2>
        <Form>
          <div className="itemList">
            <Form.Check
              type="checkbox"
              id={`check-api-aircon`}
              onChange={() => handleCheckboxChange("에어컨")}
              checked={selectedOptions.includes("에어컨")}
              label="에어컨"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-refrigerator`}
              onChange={() => handleCheckboxChange("냉장고")}
              checked={selectedOptions.includes("냉장고")}
              label="냉장고"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-kimchiRefrigerator`}
              onChange={() => handleCheckboxChange("김치냉장고")}
              checked={selectedOptions.includes("김치냉장고")}
              label="김치냉장고"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-washingMachine`}
              onChange={() => handleCheckboxChange("세탁기")}
              checked={selectedOptions.includes("세탁기")}
              label="세탁기"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-microwave`}
              onChange={() => handleCheckboxChange("전자레인지")}
              checked={selectedOptions.includes("전자레인지")}
              label="전자레인지"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-stove`}
              onChange={() => handleCheckboxChange("가스레인지")}
              checked={selectedOptions.includes("가스레인지")}
              label="가스레인지"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-electricRange`}
              onChange={() => handleCheckboxChange("전기레인지(하이라이트, 인덕션)")}
              checked={selectedOptions.includes("전기레인지(하이라이트, 인덕션)")}
              label="전기레인지(하이라이트, 인덕션)"
            />
            {/* <Form.Check
              type="checkbox"
              id={`check-api-electricRange_hiLight`}
              onChange={() => handleCheckboxChange("전기레인지(하이라이트)")}
              checked={selectedOptions.includes("전기레인지(하이라이트)")}
              label="전기레인지(하이라이트)"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-electricRange_induction`}
              onChange={() => handleCheckboxChange("전기레인지(인덕션)")}
              checked={selectedOptions.includes("전기레인지(인덕션)")}
              label="전기레인지(인덕션)"
            /> */}
            <Form.Check
              type="checkbox"
              id={`check-api-rooftop`}
              onChange={() => handleCheckboxChange("옥탑")}
              checked={selectedOptions.includes("옥탑")}
              label="옥탑"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-duplex`}
              onChange={() => handleCheckboxChange("복층")}
              checked={selectedOptions.includes("복층")}
              label="복층"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-elevator`}
              onChange={() => handleCheckboxChange("엘리베이터")}
              checked={selectedOptions.includes("엘리베이터")}
              label="엘리베이터"
            />
            {/* Add more checkboxes with unique IDs and labels */}
          </div>
        </Form>
        <button onClick={handleSaveOptions}>완료</button>
      </div>
    </div>
  );
};

export default Rag13;
