// Rag06.js
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { saveTransactionMethod } from "../action/apartmentAction";

const Rag06 = () => {
  const dispatch = useDispatch();
  const transactionMethod = useSelector((state) => state.apartment.transactionMethod);

  const handleOptionChange = (event) => {
    const transactionMethod = event.target.id;
    // Redux 액션을 디스패치하여 거래 방식 정보 저장
    dispatch(saveTransactionMethod(transactionMethod));
  };

  return (
    <div className="ragBody">
      <h1>Rag06</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>거래 방식을 선택해주세요.</h1>
        </div>
      </div>
      <div className="home_rgt_content_space">
        <div className="tx_check">
          <div className="hom_transaction_option">
            <ul>
              <li>
                <input
                  type="radio"
                  name="transaction_option"
                  id="charter"
                  checked={transactionMethod === "charter"}
                  onChange={handleOptionChange}
                />
                <label htmlFor="charter">전세</label>
              </li>
              <li>
                <input
                  type="radio"
                  name="transaction_option"
                  id="trading"
                  checked={transactionMethod === "trading"}
                  onChange={handleOptionChange}
                />
                <label htmlFor="trading">매매</label>
              </li>
              <li>
                <input
                  type="radio"
                  name="transaction_option"
                  id="monthly"
                  checked={transactionMethod === "monthly"}
                  onChange={handleOptionChange}
                />
                <label htmlFor="monthly">월세</label>
              </li>
              <li>
                <input
                  type="radio"
                  name="transaction_option"
                  id="short_term"
                  checked={transactionMethod === "short_term"}
                  onChange={handleOptionChange}
                />
                <label htmlFor="short_term">단기</label>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Rag06;
