import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";

const MetaPage = () => {
  const dispatch = useDispatch();
  const isMobile = window.navigator.userAgent.indexOf("Mobile") !== -1;
  const handleCheckChange = (event) => {
    const mataWorld = event.target.id;
    dispatch();
  };
  return (
    <div>
      <h1>mataPage</h1>
      <div className="meta-body">
        <ul>
          <li>
            <Link className="goToMetaWorld" to="/metpage/10pyeong">
              <span style={{ fontSize: "17px", cursor: "pointer" }}>10평</span>
            </Link>
          </li>
          <li>
            <Link className="goToMetaWorld" to="/metpage/20pyeong">
              <span style={{ fontSize: "17px", cursor: "pointer" }}>20평</span>
            </Link>
          </li>
          <li>
            <Link className="goToMetaWorld" to="/metpage/30pyeong">
              <span style={{ fontSize: "17px", cursor: "pointer" }}>30평</span>
            </Link>
          </li>
          <li>
            <Link className="goToMetaWorld" to="/metpage/40pyeong">
              <span style={{ fontSize: "17px", cursor: "pointer" }}>40평</span>
            </Link>
          </li>
          <li>
            <Link className="goToMetaWorld" to="/metpage/50pyeong">
              <span style={{ fontSize: "17px", cursor: "pointer" }}>50평</span>
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default MetaPage;
